@import url(https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,300;0,400;0,600;0,800;1,100;1,300;1,400;1,600;1,800&display=swap);
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: var(--light-app-header);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: var(--light-white);
}

.App-link {
  color: var(--light-link);
}

@-webkit-keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

:root{--light-primary:#7BA61F;--light-secondary:#CBCBCB;--light-third:#1B1714;--light-fourth:#413E3B;--light-error:#AC2E00;--light-success:#708C32;--light-disabled:#B5B5B5;--light-running:#FFf6AE;--light-completed:#B4CC81B0;--light-scheduled:#ffffed;--light-canceled:#DFD7D750;--light-white:#FAFCFA;--light-workset:#FEEDE1;--light-empty-workset:#FEEDE1;--light-mit-completed:#B4CC81B0;--light-mit-abandoned:#DFD7D750;--light-note:#E1F1F7C0;--light-app-header:#282c34;--light-starred:#FFE66EC0;--light-link:#C4E3EF80;--dark-primary:#7BA61F;--dark-secondary:#413E3B;--dark-third:#DFDFDF;--dark-fourth:#CBCBCB;--dark-error:#AC2E00;--dark-success:#708C32;--dark-disabled:#B5B5B5;--dark-running:#ededff;--dark-completed:#edffed;--dark-scheduled:#ffffed;--dark-canceled:#ffeeee;--dark-white:#ffffff;--dark-workset:#ffefcd;--dark-empty-workset:#fff6d6}

.color-running{background-color:var(--light-running)}.color-completed{background-color:var(--light-completed)}.color-scheduled{background-color:var(--light-scheduled)}.color-canceled{background-color:var(--light-canceled)}.color-white{background-color:var(--light-white)}.color-link{background-color:var(--light-link)}.color-starred{background-color:var(--light-starred)}.color-workset{background-color:var(--light-workset)}.color-empty-workset{background-color:var(--light-empty-workset)}.color-mit-completed{background-color:var(--light-mit-completed)}.color-mit-abandoned{background-color:var(--light-mit-abandoned)}.color-note{background-color:var(--light-note)}

.--landing-background{font-family:'Montserrat', sans-serif;background:center/cover no-repeat url(/static/media/test-tree-2.d7ca7574.jpg);display:flex;flex-direction:column;justify-content:space-between;height:100vh;position:relative;z-index:5}.--landing-background .logo-header{font-size:32px;font-weight:bold}.--landing-background .logo-img{max-width:250px;margin:0 auto}.--landing-background form{margin-top:1em}.--landing-background .navbar-light .navbar-nav{z-index:5}.--landing-background .navbar-light .navbar-nav .nav-link{color:white !important;border:1px solid white;padding:.25em .5em}.--landing-background .navbar-light .navbar-nav .nav-link:hover{color:black !important;background-color:white}.--landing-background .background-mask{position:absolute;width:100%;height:100%;background-color:rgba(0,0,0,0.6);z-index:3}.--landing-main-content{display:flex;flex-direction:column;justify-content:center;align-items:center;color:var(--light-white);z-index:5;margin-bottom:50px}.--landing-main-header{color:var(--light-white)}.--landing-secondary-header{font-size:16px}.--landing-button.btn-primary{background-color:var(--light-primary);border-color:var(--light-success)}.--landing-button.btn-primary:hover,.--landing-button.btn-primary:active{background-color:var(--light-success);border-color:var(--light-primary)}.--landing-countdown{display:flex;justify-content:center;margin-bottom:4em;z-index:5}.--landing-hours,.--landing-minutes,.--landing-seconds,.--landing-days{display:flex;flex-direction:column;align-items:center;justify-content:center;width:125px;height:125px;margin:0 1rem;font-size:64px;background-color:rgba(65,62,59,0.75);color:white}.--landing-click-link{text-decoration:underline;color:white;cursor:pointer}.--landing-click-link:hover{color:white}


body {
  overflow: hidden;
  margin: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.footer {
  position: relative;
  bottom: 0;
  width: 100%;
  height: 30px;
  line-height: 30px;
  background-color: #f5f5f5;
}

